'use client';

import { CheckCircle } from 'lucide-react';

interface Plan {
  name: string;
  price: string;
  description: string;
  features: string[];
}

interface PricingSectionProps {
  plans: Plan[];
}

export default function PricingSection({ plans }: PricingSectionProps) {
  return (
    <section className="py-20 bg-gray-50">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold text-center mb-16">
          <span className="bg-gradient-to-r from-gray-900 to-gray-700 bg-clip-text text-transparent">
            Tarification Flexible
          </span>
        </h2>
        
        <div className="grid md:grid-cols-3 gap-8 max-w-5xl mx-auto">
          {plans.map((plan, index) => (
            <div 
              key={index} 
              className={`p-8 rounded-lg transition ${
                index === 1 
                  ? 'bg-primary-600 text-white shadow-2xl scale-105' 
                  : 'bg-white shadow-lg hover:shadow-xl'
              }`}
            >
              <h3 className="text-2xl font-bold mb-2">{plan.name}</h3>
              <p className={index === 1 ? 'text-white/80' : 'text-gray-600'}>
                {plan.description}
              </p>
              
              <div className="my-6">
                <span className="text-4xl font-bold">{plan.price}</span>
                {plan.price !== 'Personnalis√©' && <span className={index === 1 ? 'text-white/80' : 'text-gray-600'}>/mois</span>}
              </div>
              
              <ul className="space-y-3 mb-8">
                {plan.features.map((feature, i) => (
                  <li key={i} className="flex items-center gap-2">
                    <CheckCircle className="w-5 h-5" />
                    {feature}
                  </li>
                ))}
              </ul>
              
              <button className={`w-full py-3 rounded-lg font-semibold transition ${
                index === 1
                  ? 'bg-white text-primary-600 hover:bg-gray-100'
                  : 'bg-primary-600 text-white hover:bg-primary-700'
              }`}>
                Choisir ce plan
              </button>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
